* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#hero{height:40vh;display:flex;justify-content:center;align-items:center;width:100%;padding:20px}#hero form{width:100%;max-width:650px;display:flex}#hero form input{width:95%;border:0 solid transparent;box-shadow:none;height:65px;padding-left:30px;font-size:14px;border-radius:4px;outline:none}#hero form button{margin-left:-5px;padding:5px 25px;font-size:20px;background:#10b3d6;color:#fff;text-align:center;border:0;border-radius:4px;border-top-left-radius:0;border-bottom-left-radius:0;transition:all 0.4s linear}

#nav-bar-conatiner{color:#ffffff;display:flex;justify-content:space-between;padding:20px;align-items:center}#nav-bar-conatiner #nav{display:flex;list-style:none;height:100%;align-items:center}#nav-bar-conatiner #nav .nav-item{margin-left:20px;color:#ffffff}#nav-bar-conatiner #nav .nav-item:hover{color:#10b3d6}#nav-bar-conatiner #nav .nav-item-active{color:#10b3d6}#nav-bar-conatiner #nav .nav-button{font-size:14px;font-weight:500;padding:2px 10px;text-align:center;border-radius:4px;border:2px solid #fff;transition:all 0.3s linear;margin-left:20px;background:#fff;color:#10b3d6;padding-top:10px;padding-bottom:10px;margin-right:20px;max-height:50px}#nav-bar-conatiner #nav .nav-button:hover{border-color:#10b3d6;background-color:#10b3d6;color:#fff;cursor:pointer}@media only screen and (max-width: 600px){.nav-item{display:none}.nav-button{display:block}}@media only screen and (max-width: 500px){.nav-button{font-size:10px}}#profile-circle{width:30px;height:30px;border-radius:50%;display:flex;justify-content:center;align-items:center;background-color:#fff;color:#10b3d6;margin-left:10px;cursor:pointer;margin-right:10px}#prifile-dropdow{position:absolute;top:70px;right:30px;background:#ffffff;color:#10b3d6;padding:20px}#prifile-dropdow #logout{cursor:pointer}

.modal{display:none;position:fixed;z-index:1;left:0;top:0;width:100%;height:100%;overflow:auto;background-color:#000;background-color:rgba(0,0,0,0.4);-webkit-animation-name:fadeIn;-webkit-animation-duration:0.4s;animation-name:fadeIn;animation-duration:0.4s}.modal-content{position:fixed;top:50%;background-color:#fefefe;left:50%;transform:translate(-50%, -50%);-webkit-animation-name:slideIn;-webkit-animation-duration:0.4s;animation-name:slideIn;animation-duration:0.4s}.close{color:white;float:right;font-size:28px;font-weight:bold}.close:hover,.close:focus{color:#000;text-decoration:none;cursor:pointer}.modal-header{padding:2px 16px;background-color:#10b3d6;color:white}.modal-body{padding:2px 16px}.modal-footer{padding:2px 16px;background-color:#5cb85c;color:white}@-webkit-keyframes slideIn{from{top:-300px;opacity:0}to{top:50%;opacity:1}}@keyframes slideIn{from{top:-300px;opacity:0}to{top:50%;opacity:1}}@-webkit-keyframes fadeIn{from{opacity:0}to{opacity:1}}@keyframes fadeIn{from{opacity:0}to{opacity:1}}@media screen and (max-width: 450px){.modal-content{width:100% !important}}

#register-outer{display:flex;flex-direction:column;align-items:center;width:100%;padding:20px;background-image:linear-gradient(60deg, #10b3d6 0%, #1d2746 100%);height:100vh}#register-outer h1{margin-top:30px;color:#ffffff}#register-outer #register{border:1px #ddd solid;padding:20px;background-color:#ffffff;margin-top:30px;width:400px}#register-outer #register #register-form{display:flex;flex-direction:column}#register-outer #register #register-form input,#register-outer #register #register-form textarea{margin-top:5px;border:1px solid #10b3d6;outline:none;width:100%;height:35px}#register-outer #register #register-form textarea{padding:7px}#register-outer #register #register-form label{margin-top:15px;font-size:13px}#register-outer #register #register-form .btn{width:100%;font-size:14px;font-weight:400;color:#fff;border-radius:4px;background:#10b3d6;display:inline-block;padding:12px 35px;transition:color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.18s ease-in-out;border:none;outline:none;margin-top:25px;cursor:pointer}#register-outer #register #register-form .btn:hover{box-shadow:0 20px 30px 0 rgba(12,118,142,0.24)}#register-outer #register #register-form .error{color:#c40000;font-size:14px}#register-outer #register #alreay-member{display:block;margin-top:20px;font-size:13px}@media screen and (max-width: 450px){#register{width:100% !important}}

.header {
  background-image: linear-gradient(60deg, #10b3d6 0%, #1d2746 100%);
}
a {
  text-decoration: none;
}
.link {
  color: #1d2746;
}
.link:hover {
  color: #10b3d6;
}
@media only screen and (max-width: 500px) {
  h1 {
    font-size: 19px !important;
  }
}

#forum{margin:15px;display:flex;justify-content:space-between}#forum .num{background:#ebedf0;padding:4px 7px 2px;border-radius:10px;color:#838793;font-size:14px;display:inline-block;line-height:1}

.post-container{margin-bottom:20px}.post-container .post-hadding{color:#1d2746;margin-bottom:7px}.post-container .post-hadding:hover{color:#10b3d6}.post-container .post-body{display:block;display:-webkit-box;width:100%;max-height:2.2rem;margin:0 auto;font-size:1rem;line-height:1.1rem;-webkit-line-clamp:2;-webkit-box-orient:vertical;overflow:hidden;text-overflow:ellipsis}.post-container .comment-number{color:#10b3d6}.post-container .comment-number span{color:black}

#home{margin-top:20px;padding:20px;width:100%;display:flex;justify-content:space-between}#home #left{width:65%}#home #right{width:30%;padding:20px}@media only screen and (max-width: 685px){#home{display:block}#home #left{width:100%}#home #right{width:100%}}

#post-comment{font-size:14px;font-weight:400;color:#fff;border-radius:4px;background:#10b3d6;display:inline-block;padding:12px 35px;transition:color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.18s ease-in-out;border:none;outline:none;margin-top:25px;cursor:pointer;margin-left:70%}#post-comment:hover{box-shadow:0 20px 30px 0 rgba(12,118,142,0.24)}#reply-hadding{margin-top:20px;margin-bottom:20px}

#comment-form form{width:100%;max-width:650px;display:flex;margin-top:35px}#comment-form form input{width:95%;border:0 solid transparent;box-shadow:none;height:65px;padding-left:30px;font-size:14px;border-radius:4px;outline:none;border:1px solid #10b3d6}#comment-form form button{margin-left:-5px;padding:5px 25px;font-size:20px;background:#10b3d6;color:#fff;text-align:center;border:0;border-radius:4px;border-top-left-radius:0;border-bottom-left-radius:0;transition:all 0.4s linear}

#sidebar{padding:20px;background-color:#f6f9fa;width:250px;height:100vh}#sidebar .item div{background-color:#edeff1;padding:10px}#sidebar .item-active{color:#ffffff}#sidebar .item-active div{background-color:#10b3d6;padding:10px}

#admin{display:flex}#admin #admin-right{padding:20px}#admin-form{margin-top:20px;margin-bottom:20px}#admin-form input{width:250px;height:30px;outline:none;margin-bottom:10px}#admin-form button{height:30px;background-color:#10b3d6;border:none;outline:none;width:auto;padding:0px 20px;margin-top:20px}.admin-active{color:#10b3d6}.admin-active span{color:black}#button{background-color:#10b3d6;padding:10px;border:none;outline:none;margin-top:20px;margin-bottom:20px;cursor:pointer}

